exports.install = function (Vue, options) {


    Vue.prototype.init = function () {

        if (window.plus) {
            Vue.prototype.$h5plus = window.plus;
            this.app_init();
        } else {
            document.addEventListener("plusready", () => {
                Vue.prototype.$h5plus = window.plus;
                console.log('call init[plusready]');
                this.$hub.emit('event:plusready', {});
                this.app_init();
            }, false);
        }

        console.log('window.plus:', window.plus);
        console.log('$h5plus:', this.$h5plus);

        Vue.prototype.init_data = {
            'now_time': (new Date().getTime()) / 1000
        }
        var $web_conf = this.$web_conf;
        var cn_tmp_hosts = $web_conf['cn_tmp_hosts'];
        this.$web_conf.set_host = cn_tmp_hosts[0];
        this.$web_conf.lang = 'chinese';
        this.$web_conf.current_host = document.domain;


        this.baidu_push();
        this.load_lang();
        if (this.create_axios) {
            this.create_axios();
        }
        if (this.init_traffic) {
            this.init_traffic();
        }
        this.update_row_count();

        this.$axios.get("/app/cha/init", {
            params: {
                'f': this.get_from_source()
            }
        }).then((response) => {
            this.$store.commit('init', {
                'data': response.data,
                'time': new Date().valueOf()
            });

            let server_time = response.data.server_time;

        }).catch((error) => {
            console.log("init error:", error)

        });
        this.update_user_info();

    };



};

