import Vue from "vue";

import '@/assets/css/video.css'
import './assets/css/app.css'
import './assets/css/extra.css'
import './assets/css/ruby.light.css'
import './assets/css/ruby_china.css'
import './assets/css/forum.css'
import './assets/css/theme.css'
import './assets/css/51.css'
import './assets/css/jvid.css'
import './assets/css/golbal.css'
import './assets/mobile/app.css'


export default {
  name: 'App',
  components: {
    user_dialog: () => import('@/components/user/user.dialog'),
    app_tabbar: () => import('@/components/app/tabbar'),
  },
  data() {
    return {
      header_show: true,
      footer_show: true,
      header_margin_top: 0,
      gcontainer_width: undefined,
      golbaldialog: undefined,
      dialog_text: '',
      is_support_webp: false
    }
  },
  created() {


    const FastClick = require('fastclick');
    FastClick.attach(document.body);

    //dark 模式
    this.$vuetify.theme.dark = false;
    this.$q.dark.set(false);

    //判断是不是APP


    window.app = this;
    Vue.prototype.$app = this;

    this.$nextTick(() => {
      this.init_header_height();
      this.is_support_webp = this.isSupportWebp();

      if (this.$refs['gcontainer']) {
        this.gcontainer_width = this.$refs['gcontainer'].clientWidth;
        console.log('gcontainer_width:', this.gcontainer_width);
      }

    });

    console.log('process.env.VUE_APP_VERSION :', process.env.VUE_APP_VERSION);
  },
  methods: {
    isSupportWebp: function () {
      try {
        return document.createElement('canvas').toDataURL('image/webp', 0.5).indexOf('data:image/webp') === 0;
      } catch (err) {
        return false;
      }
    },
    init_header_height: function () {
      console.log('call init_header_height');
      if (this.$refs.appheader) {
        if (!this.$refs.appheader.header_height || this.$refs.appheader.header_height < 1) {
          return setTimeout(this.init_header_height, 100);
        }
      }

      try {
        console.log('appheader height:', this.$refs.appheader.header_height);
        this.header_margin_top = this.$refs.appheader.header_height + 5;
      } catch (e) {
        this.header_margin_top = 0;
      }

      console.log('header_margin_top:', this.header_margin_top);
    },
    //是否显示头部
    header: function (bool) {
      this.header_show = bool;
    },
    //是否显示底部
    footer: function (bool) {
      this.footer_show = bool;
    }
  }
}
