if (process.env.VUE_APP_WEBAPP) {
    exports.install = function (Vue, options) {

        Vue.prototype.app_init = function () {
            if (this.$h5plus) {
                this.$h5plus.geolocation.getCurrentPosition(function (p) {
                    alert('Geolocation\nLatitude:' + p.coords.latitude + '\nLongitude:' + p.coords.longitude + '\nAltitude:' + p.coords.altitude);
                }, function (e) {
                    alert('Geolocation error: ' + e.message);
                });


                document.addEventListener("pause", () => {
                    //从前台切换到后台
                    console.log('call event pause');
                }, false);

                document.addEventListener("resume", () => {
                    //从后台切换到前台
                    console.log('call event resume');
                }, false);


                document.addEventListener("netchange", () => {
                    //网络状态变化
                    console.log('call event netchange');

                }, false);


                document.addEventListener("error", function (e) {

                }, false);


            }
        };
    };

}

