import Vue from 'vue'
import VueAnalytics from 'vue-analytics'


import './quasar'
import vuetify from './plugins/vuetify';

import './modules/app'
import './modules/common'
import './modules/requests'
import './modules/forum'
import './modules/ads'
import './modules/traffic'
import './modules/lang'
import './modules/video'
import './modules/h5plus'
import store from './modules/store'
import flutter from './modules/flutter'
import base from './modules/base'
import chabase from './modules/chabase'
import './modules/js.extend'
import './vant'


Vue.use(flutter)
Vue.use(base)
Vue.use(chabase)

import VueLazyload from 'vue-lazyload'

Vue.use(VueLazyload, {
    preLoad: 1.1,
    attempt: 1
});

import router from './router'
import cha_item from '@/components/cha/item/cha_item'
import item_tip from './components/items/tip'
import topic_item from './components/forum/items/topic_item'
import video_item from './components/video/item/video_item'
import header from './components/header'
import footer from './components/footer'
import cha_list from './components/cha/item/cha_list'
import page_loading from './components/common/page.loading'
import tags from './components/common/tags'

Vue.config.productionTip = false

Vue.component('tags', tags)
Vue.component('page_loading', page_loading)
Vue.component('video_item', video_item)
Vue.component('cha_list', cha_list)
Vue.component('item_tip', item_tip)
Vue.component('appheader', header)
Vue.component('appfooter', footer)
Vue.component('topic_item', topic_item)
Vue.component('cha_item', cha_item)

import App from './App.vue'

Vue.mixin({
    computed: {
        plusReady() {
            return this.$store.state.isplusReady;
        }
    }
});

// 配置html plus 环境
document.addEventListener('plusready', function () {
    // 设置一个全局plusready状态
    store.state.isplusReady = true;

    // 监听返回按键
    let now = false;
    let time = null;
    window.plus.key.addEventListener('backbutton', function () {
        // 如果是首页则执行退出操作
        if (window.gapp.$route.path === "/home" || window.gapp.$route.path === "/my-center") {
            time = null;
            if (now) {
                now = false;
                window.plus.runtime.quit();
            } else {
                now = true;
                window.plus.nativeUI.toast("再按一次退出", {duration: 'short'});
                time = setTimeout(() => {
                    now = false;
                }, 1000);
            }
            // 如果是内页则执行路由返回操作
        } else {
            router.go(-1);
        }

    })
});

window.gapp = new Vue({
    render: h => h(App),
    vuetify,
    router: router,
    store,
    beforeCreate() {
        setTimeout(() => {
            if (this.$web_conf.analytics) {
                console.log('this.$web_conf.analytics:', this.$web_conf.analytics);
                Vue.use(VueAnalytics, {
                    id: this.$web_conf.analytics['google'],
                    router,
                    autoTracking: {
                        pageviewOnLoad: false
                    }
                });
            }
            ;
        }, 500);
    },
    created() {
        this.$store.dispatch('init');
        this.init();
    }
}).$mount('#app')


